.profileScreen {
    height: 100vh;
    color: #fff;
  }
  
  .profileScreen__body {
    display: flex;
    flex-direction: column;
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 5%;
    max-width: 800px;
  }

.plansScreen__info> p{
    border-bottom: 1px solid #282c2d;
    padding-bottom: 5px;
    font-size: 10px;
}
  
  .profileScreen__info {
    display: flex;
  }
  
  .profileScreen__details {
    color: #fff;
    margin-left: 25px;
    flex: 1 1;
  }
  
  .profileScreen__details > h2 {
    background-color: grey;
    font-size: 15px;
    padding: 15px 15px 15px 20px;
  }
  
  .profileScreen__signOut {
    padding: 10px 20px;
    font-size: 1rem;
    margin-top: 5%;
    width: 50%;
    color: #fff;
    background-color: #e50914;
    outline-width: 0;
    border: none;
    cursor: pointer;
  }
  
  .profileScreen__plans {
    margin-top: 20px;
  }
  
  .profileScreen__plans > h3 {
    border-bottom: 1px solid #282c2d;
    padding-bottom: 10px;
  }
  
  .profileScreen__body > h1 {
    font-size: 60px;
    font-weight: 400;
    border-bottom: 1px solid #282c2d;
    margin-bottom: 20px;
  }
  
  .profileScreen__info > img {
    height: 100px;
  }